* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

.EmptyTodo {
    width: 100%;
    height: 60vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.EmptyTodo__Title {
    color: #E4E6EA;
}
.EmptyTodo__SubTitle {
    font-weight: 400;
    color: #E4E6EA;
}